  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

#root,
#__next {
  isolation: isolate;
}

:root {
  --system-ui: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  --dark-blue: #191970;
  --hover-blue: #272763;
  --active-blue: #353555;
  --white: #f9faf8;
  --focus-blue: #67c2e0;
}

body {
  font-family: var(--system-ui);
  padding: 1em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1em;
  background-color: var(--dark-blue);
  color: var(--white);
}

button {
  background-color: var(--dark-blue);
  color: var(--white);
  border: 2px solid var(--white);
  cursor: pointer;
}

header > h1 {
  font-size: 3rem;
}

form {
  display: flex;
  font-size: 1.5rem;
}

form > div {
  display: flex;
  flex-direction: column;
}

label {
  padding-left: 10px;
}

input {
  background-color: var(--dark-blue);
  border: 2px solid var(--white);
  color: var(--white);
  outline: none;
  border-bottom-left-radius: 10px;
  border-top-left-radius: 10px;
  padding-left: 10px;
}

input:focus {
  border-color: var(--focus-blue);
}

.result {
  font-size: 1.5rem;
}

.search {
  height: 1.5em;
}

.toggle-wrapper {
  align-content: center;
}

.toggle {
  font-size: 2rem;
  padding: 5px 10px;
  border-radius: 10px;
}

.toggle:hover,
.btn > button:hover {
  background-color: var(--hover-blue);
}

.toggle:active,
.btn > button:active {
  background-color: var(--active-blue);
}

.btn {
  justify-content: flex-end;
}

.btn > button {
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
}

.container {
  justify-items: center;
  gap: 1em;
}

.content {
  display: flex;
  justify-content: center;
  gap: 2em;
  padding: 1em;
}

.current-weather {
  display: flex;
  gap: 1.5em;
}

.temp-wrapper {
  display: flex;
}

.temperature {
  font-size: 6rem;
}

span {
  font-size: 3rem;
}

.info {
  align-content: center;
  justify-items: center;
  gap: 1em;
}

.info > img {
  height: 58px;
}

.location {
  font-size: 2rem;
}

.condition,
.description {
  font-size: 1.5rem;
}

.week {
  display: flex;
  gap: 1.5em;
  margin-bottom: 2em;
}

.day-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 0.25em;
  padding: 8px;
  font-weight: 900;
}

.day-card > img {
  height: 42px;
}

.giphyGif {
  border: 2px solid var(--white);
  border-radius: 10px;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;;;CAGC;;AAED;;;EAGE,sBAAsB;AACxB;;AAEA;EACE,SAAS;AACX;;AAEA;EACE;IACE,gCAAgC;EAClC;AACF;;AAEA;EACE,gBAAgB;EAChB,mCAAmC;AACrC;;AAEA;;;;;EAKE,cAAc;EACd,eAAe;AACjB;;AAEA;;;;EAIE,aAAa;AACf;;AAEA;;;;;;;EAOE,yBAAyB;AAC3B;;AAEA;EACE,iBAAiB;AACnB;;AAEA;;;;;;EAME,kBAAkB;AACpB;;AAEA;;EAEE,kBAAkB;AACpB;;AAEA;EACE,mJAAmJ;EACnJ,oBAAoB;EACpB,qBAAqB;EACrB,sBAAsB;EACtB,gBAAgB;EAChB,qBAAqB;AACvB;;AAEA;EACE,6BAA6B;EAC7B,YAAY;EACZ,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,mBAAmB;EACnB,QAAQ;EACR,kCAAkC;EAClC,mBAAmB;AACrB;;AAEA;EACE,kCAAkC;EAClC,mBAAmB;EACnB,8BAA8B;EAC9B,eAAe;AACjB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,aAAa;EACb,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,sBAAsB;AACxB;;AAEA;EACE,kBAAkB;AACpB;;AAEA;EACE,kCAAkC;EAClC,8BAA8B;EAC9B,mBAAmB;EACnB,aAAa;EACb,+BAA+B;EAC/B,4BAA4B;EAC5B,kBAAkB;AACpB;;AAEA;EACE,+BAA+B;AACjC;;AAEA;EACE,iBAAiB;AACnB;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,eAAe;EACf,iBAAiB;EACjB,mBAAmB;AACrB;;AAEA;;EAEE,mCAAmC;AACrC;;AAEA;;EAEE,oCAAoC;AACtC;;AAEA;EACE,yBAAyB;AAC3B;;AAEA;EACE,gCAAgC;EAChC,6BAA6B;AAC/B;;AAEA;EACE,qBAAqB;EACrB,QAAQ;AACV;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,QAAQ;EACR,YAAY;AACd;;AAEA;EACE,aAAa;EACb,UAAU;AACZ;;AAEA;EACE,aAAa;AACf;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,eAAe;AACjB;;AAEA;EACE,qBAAqB;EACrB,qBAAqB;EACrB,QAAQ;AACV;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,eAAe;AACjB;;AAEA;;EAEE,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,UAAU;EACV,kBAAkB;AACpB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,8BAA8B;EAC9B,mBAAmB;EACnB,WAAW;EACX,YAAY;EACZ,gBAAgB;AAClB;;AAEA;EACE,YAAY;AACd;;AAEA;EACE,8BAA8B;EAC9B,mBAAmB;AACrB","sourcesContent":["/*\n  Josh's Custom CSS Reset\n  https://www.joshwcomeau.com/css/custom-css-reset/\n*/\n\n*,\n*::before,\n*::after {\n  box-sizing: border-box;\n}\n\n* {\n  margin: 0;\n}\n\n@media (prefers-reduced-motion: no-preference) {\n  html {\n    interpolate-size: allow-keywords;\n  }\n}\n\nbody {\n  line-height: 1.5;\n  -webkit-font-smoothing: antialiased;\n}\n\nimg,\npicture,